import axios from 'axios'
import qs from 'qs'
import md5 from 'md5'
import LRU from 'lru-cache'
import env from '../config/env'

// 
let config
if (process.__API__) {
    config = process.__API__
} else {
    config = process.__API__ = {
        api: env.serverUrl + '/diverhome/rest/',
        // 15
        cached: LRU({
            max: 1000,
            maxAge: 1000 * 60 * 15
        })
    }
}

export default {
    // post
    post(url, data, headers) {
        const key = md5(url + JSON.stringify(data))
        if (config.cached && config.cached.has(key)) {
            return new Promise((resolve, reject) => {
                resolve(config.cached.get(key));
            })
        }
        // post
        return new Promise((resolve, reject) => {
            axios({
                method: 'post',
                url: config.api + url,
                // jwttokenheader
                headers: { customToken: localStorage.getItem("customToken") },
                // 
                data: qs.stringify(data),
            }).then(res => {
                // cachetrue
                if (config.cached && data.cache) config.cached.set(key, res.data)
                    // axiosdata
                resolve(res.data)
            }).catch(function(error) {
                reject()
            });
        })
    },
    // get
    get(url, data, headers) {
        const key = md5(url + JSON.stringify(data))
        if (config.cached && config.cached.has(key)) {
            return new Promise((resolve, reject) => {
                resolve(config.cached.get(key));
            })
        }
        // get
        return new Promise((resolve, reject) => {
            axios.get(config.api + url, {
                headers: { customToken: localStorage.getItem("customToken") },
                params: data
            }).then(res => {
                resolve(res.data)
            }).catch(error => {
                reject()
            })
        })
    }
}


